.sb-control-set {
  align-items: center;
  display: flex;
}

.sb-control-set_vertical {
  flex-direction: column;

  .sb-control {
    margin: 0.5rem;
    width: 100%;
  }
}

.sb-control-set_horizontal:not(.sb-control-set_segmented) {
  flex-direction: row;

  .sb-control {
    border-radius: 0;
    border-width: 1px 1px 1px 0;
    margin: 0;

    &:first-child {
      border-left-width: 1px;
      border-radius: var(--sbt-border-radius-large) 0 0
        var(--sbt-border-radius-large);
    }

    &:last-child {
      border-radius: 0 var(--sbt-border-radius-large)
        var(--sbt-border-radius-large) 0;
    }
  }

  .sb-control_selected:not(:last-child) {
    border-right: 1px solid rgba(#fff, 0.5);
  }

  .sb-control_unselected {
    border-color: var(--sb-control-color-primary);
  }
}

.sb-control-set_horizontal.sb-control-set_segmented {
  flex-direction: row;

  .sb-control {
    margin: 0.5rem;
  }
}
