@import '../../styles/mixins';

// SB Control Variables:
// --sb-control-border-width
// --sb-control-border-radius
// --sb-control-unselected-background-color
// --sb-control-unselected-border-color
// --sb-control-unselected-color
// --sb-control-selected-background-color
// --sb-control-selected-border-color
// --sb-control-selected-color
// --sb-control-selected--hover-background-color
// --sb-control-unselected--hover-background-color
// --sb-control-color - background and outline
// --sb-control-color-contrast - text color
// --sb-control-color-dark - hover text color
// --sb-control-color-light - hover background

.sb-control {
  border-radius: var(
    --sb-control-border-radius,
    var(--sbt-border-radius-large)
  );
  border-style: solid;
  border-width: var(--sb-control-border-width, 2px);
  cursor: pointer;
  display: inline-block;
  font-size: var(--sbt-font-size-30);
  font-weight: var(--sbt-font-weight-40);
  line-height: var(--sbt-line-height-ui);
  padding: 0.75em 1.25em;
  text-align: center;
  text-decoration: none;
  transition: all 0.2s ease;
}

.sb-control_unselected {
  background-color: var(--sb-control-unselected-background-color, transparent);
  border-color: var(
    --sb-control-unselected-border-color,
    var(--sbt-theme-tertiary-color-50)
  );
  color: var(--sb-control-unselected-color, var(--sb-control-color));

  &:hover {
    background-color: var(
      --sb-control-unselected--hover-background-color,
      var(--sb-control-color-light)
    );
  }
}

.sb-control_selected {
  background-color: var(
    --sb-control-selected-background-color,
    var(--sb-control-color)
  );
  border-color: var(
    --sb-control-selected-border-color,
    var(--sb-control-color)
  );
  color: var(
    --sb-control-selected-color,
    var(--sb-control-color-contrast, var(--sbt-color-text-invert))
  );

  &:hover {
    --sb-control-color: var(
      --sb-control-selected--hover-background-color,
      var(--sb-control-color-dark)
    );
  }
}

// Color type variants

.sb-control_primary {
  --sb-control-color: var(
    --sb-control-primary-color,
    var(--sbt-theme-color-70)
  );
  --sb-control-color-dark: var(
    --sb-control-primary-color-dark,
    var(--sbt-theme-color-80)
  );
  --sb-control-color-light: var(
    --sb-control-primary-color-light,
    var(--sbt-theme-color-10)
  );
}

.sb-control_secondary {
  --sb-control-color: var(--sb-control-secondary-color, var(--sbt-slate-60));
  --sb-control-color-dark: var(
    --sb-control-secondary-color-dark,
    var(--sbt-slate-70)
  );
  --sb-control-color-light: var(
    --sb-control-secondary-color-light,
    var(--sbt-sky-10)
  );
}
